#resumeBtn{
    font-family: 'Poppins', sans-serif;
    font-size: 25px;
    font-weight: bold;
    color: #fee9e1;
    background-color: #64b6ac;
    padding: 1.5rem;
    border-radius: 2rem;
    border: none;
    margin: 1rem;
}

#resumeBtn:hover{
    color: #c0fdfb;
}

#resumeBtn:active{
    background-color: #b09e99;
}

.resumeimg{
    height: 875px;
    width:fit-content;
}

@media only screen and (max-width: 950px) {
   .resumeimg{
    height:780px;
    width: fit-content;
   }
  }

@media only screen and (max-width: 650px) {
    /* Media query Test */
    #resumeBtn{
        font-size: 15px;
        padding: 1rem;

    }
    .resumeimg{
        height:380px;
        width: fit-content;
       }
  }

  @media only screen and (max-width: 300px) {
    .resumeimg{
     height:280px;
     width: fit-content;
    }
   }
 